<template>
  <div class="q-pa-md q-gutter-y-md">
    <h4 class="q-mt-none q-mb-md text-weight-bold">Settings</h4>
    <Options />
    <AppButton text="Update" :action="updateApp" color="green" />
    <div v-if="canReset">
      <AppButton text="Reset Password" :action="resetPassword" color="grey" />
    </div>

    <AppButton text="Logout" :action="logoutHandler" color="red" />
    <span class="versionTxt">Version: {{ K.version }}</span>
  </div>
</template>

<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import useLogout from "../composables/useLogout";
import Options from "../components/Options.vue";
import AppButton from "../components/AppButton.vue";
import CreateWallet from "../components/CreateWallet.vue";
import * as K from "../constants/constants";
import {
  dialogHandler,
  storage,
  readWallet,
  resetPassword,
} from "../composables/handlers";

export default {
  name: "Settings",
  components: { Options, CreateWallet, AppButton },
  emits: ["reload"],
  setup(props, context) {
    const router = useRouter();
    const wallet = ref({});
    const canReset = ref(false);

    const initData = async () => {
      wallet.value = await readWallet();
      if (wallet.value.profile.provider == "password") {
        canReset.value = true;
      }
    };

    initData();

    const { logout, outError } = useLogout();

    const logoutHandler = async () => {
      dialogHandler({
        title: "Confirm Logout?",
        message:
          "All local storage will be cleared upon logging out, please make sure your mnemonic is backed up!",
        persistent: true,
        html: true,
        ok: {
          label: "Confirm",
          style: { "text-transform": "none" },
          color: "red",
        },
        cancel: {
          label: "Cancel",
          style: { "text-transform": "none" },
        },
      }).onOk(async () => {
        router.replace({ name: "Account" });
        await logout();
        if (!outError.value) {
          K.print("user logged out");
        }
      });
    };

    const reloadInfo = async () => {
      context.emit("reload");
    };

    const updateApp = async () => {
      dialogHandler({
        title: "Confirm Update?",
        message: "Please close and restart the app after update.",
        persistent: true,
        ok: {
          label: "Understand",
          style: { "text-transform": "none" },
          color: "green",
        },
        cancel: {
          label: "Cancel",
          style: { "text-transform": "none" },
        },
        html: true,
      }).onOk(async () => {
        try {
          await router.replace({ name: "Account" });
          await storage.remove("account");
          await storage.remove("balances");
          await storage.remove("myBids");
          await storage.remove("colContent");
          await storage.remove("colIndex");
          await storage.remove("nftContent");
          await storage.remove("nftIndex");
          window.location.reload(true);
        } catch (err) {
          K.error(err.message);
        }
      });
    };

    return {
      logoutHandler,
      reloadInfo,
      K,
      updateApp,
      wallet,
      canReset,
      resetPassword,
    };
  },
};
</script>

<style scoped>
.bottom {
  margin-top: 30px;
}
.versionTxt {
  color: grey;
}
</style>