<template>
  <div class="q-pa-md q-gutter-y-md">
    <div v-if="!card">
      <div v-show="!loaded" style="text-align: center">
        <q-spinner-grid color="primary" size="2.5rem" />
      </div>
      <div v-show="loaded">
        <div v-if="!user || !isInstalled">
          <h4 class="q-mt-none q-mb-md text-weight-bold">
            Hi! This is Teamcoin!
          </h4>
          <p>Proof of Performance proof-of-concept</p>
          <div style="text-align: center">
            <div v-if="isInstalled">
              <UserAuth @reload="reloadInfo" />
            </div>
            <div v-else>
              <AppButton text="About" :action="toAbout" />
              <AppButton text="Install" :action="installHandler" />
            </div>
          </div>
        </div>
        <div v-else>
          <div v-if="!wallet.disclose">
            <Options @reload="reloadInfo" />
          </div>
          <div v-else-if="!wallet.disclose.zid">
            <h4 class="q-mt-none q-mb-md text-weight-bold">Wallet</h4>
            <p>
              Please get some pen and paper to write down your 12-word mnemonic,
              keep it safe and never reveal it to anyone!
            </p>
            <CreateWallet @reload="reloadInfo" />
          </div>
          <div v-else>
            <span v-if="username">
              <h5
                class="q-mt-none q-mb-none text-weight-bold text-center"
                :class="{ forcewrap: wrapUsername }"
              >
                {{ username }}
              </h5>
            </span>
            <span v-else>
              <h4 class="q-mt-none q-mb-md text-weight-bold">Balances</h4>
            </span>
            <div v-if="isLocked" style="text-align: center">
              <!-- <AppButton text="Unlock" :action="unlockMe" />
            <AppButton text="Take" :action="toReceive" /> -->
              <q-footer>
                <div
                  class="row q-px-lg q-mt-sm justify-between btnStyle"
                  style="max-width: 700px"
                >
                  <div class="q-my-sm q-mb-md text-center">
                    <q-btn
                      outline
                      rounded
                      color="white"
                      @click="toReceive"
                      no-caps
                    >
                      <div v-show="windowWidth >= 360">
                        <q-icon color="white" name="south_east" />
                        <q-icon color="white" :name="iconSet[K.asset]" />
                      </div>
                      <span :class="{ 'q-pl-sm': windowWidth >= 360 }"
                        >Take</span
                      >
                    </q-btn>
                  </div>
                  <div class="q-my-sm q-mb-md text-center btnStyle">
                    <q-btn
                      outline
                      rounded
                      color="white"
                      @click="unlockMe"
                      no-caps
                    >
                      <span :class="{ 'q-pr-sm': windowWidth >= 360 }"
                        >Unlock</span
                      >
                      <div v-show="windowWidth >= 360">
                        <!-- <q-icon color="white" :name="iconSet[K.asset]" /> -->
                        <q-icon color="white" name="lock_open" />
                      </div>
                    </q-btn>
                  </div>
                </div>
              </q-footer>
            </div>
            <div v-else>
              <Balances />
            </div>
            <div v-show="!isLocked">
              <AppButton text="Lock" :action="lockMe" />
              <div v-if="isDesktop && false">
                <AppButton text="Hardware Wallet" :action="getStrPublicKey" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <p class="head">Desktop</p>
      <p>
        1. Go to
        <a target="_blank" :href="poclink"
          ><span class="clickSpan">Popcoin POC</span></a
        >
        using Chrome.
      </p>
      <p>2. Press "Install".</p>
      <p class="head">Android</p>
      <p>
        1. Go to
        <a target="_blank" :href="poclink"
          ><span class="clickSpan">Popcoin POC</span></a
        >
        using Chrome.
      </p>
      <p>2. Press "Install app".</p>
      <p class="head">iOS</p>
      <p>
        1. Go to
        <a target="_blank" :href="poclink"
          ><span class="clickSpan">Popcoin POC</span></a
        >
        using Safari.
      </p>
      <p>2. Press "Add to Home Screen".</p>
      <AppButton text="Back" :action="goBack" />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import UserAuth from "../components/UserAuth.vue";
import { useRouter } from "vue-router";
import getUser from "../composables/getUser";
import CreateWallet from "../components/CreateWallet.vue";
import Options from "../components/Options.vue";
import Balances from "../components/Balances.vue";
import { Encrypt } from "../composables/cipherHandler";
import {
  readWallet,
  getPWADisplayMode,
  storage,
  dialogHandler,
  confirmPin,
  notifyHandler,
  isDesktop,
} from "../composables/handlers";
import * as K from "../constants/constants";
import AppButton from "../components/AppButton.vue";
import { dataLink } from "../secrets/secrets";
import { poclink } from "../constants/appLinks";
const axios = require("axios");
import { iconSet } from "../constants/iconSet";
import { getStrAppVersion, getStrPublicKey } from "../composables/hwsupport";

export default {
  name: "Account",
  components: { UserAuth, CreateWallet, Options, Balances, AppButton },
  emits: ["reload"],
  setup(props, context) {
    const loaded = ref(false);
    const { user } = getUser();
    const username = ref(null);
    const wrapUsername = ref(false);
    const wallet = ref({});
    const isInstalled = ref(false);
    const card = ref(false);
    const router = useRouter();
    const isLocked = ref(false);
    const appBlock = ref(false);

    const reloadInfo = async () => {
      const appStatus = getPWADisplayMode();
      if (
        appStatus == "standalone" ||
        (process.env.NODE_ENV != "production" && K.devMode)
      ) {
        isInstalled.value = true;
      }
      wallet.value = await readWallet();

      if (wallet.value == {}) {
        return;
      }
      try {
        let env = "";
        if (K.devMode) {
          env = "dev/";
        }
        const url = `${dataLink}${env}blocklist/${wallet.value.disclose.zid}.json`;
        let response = await axios.get(url);
        let responseJson = response.data;
        K.print(responseJson);
        let message = "Account is on blocklist.";
        K.error(message);
        throw new Error(message);
      } catch (err) {
        if (err.message == "Account is on blocklist.") {
          notifyHandler({
            message: "Account is blocked!",
            color: "negative",
          });
          loaded.value = true;
          appBlock.value = true;
          await lockApp();
          return;
        }
      }
      if (appBlock.value == false) {
        try {
          wallet.value = await readWallet();
          if (wallet.value.secure.mnemonic) {
            if (wallet.value.secure.locked) {
              isLocked.value = true;
            }
          } else {
            if (wallet.value.disclose.zid) {
              K.print("go to mnemonic");
              await router.replace({
                name: "Mnemonic",
                params: {
                  mnemonic: "Please restore mnemonic",
                  backTo: "Account",
                },
              });
            }
          }
        } catch (err) {
          K.print("app started");
        }
      }

      loaded.value = false;
      if (wallet.value.disclose) {
        username.value = wallet.value.disclose.username;
        const txt = username.value;
        const tmpArr = txt.split(" ");
        const arrLen = tmpArr.map((e) => {
          return e.length;
        });
        const maxLength = Math.max(...arrLen);
        if (maxLength > 20) {
          wrapUsername.value = true;
        }
      }
      context.emit("reload");
      loaded.value = true;
    };
    reloadInfo();

    const installHandler = () => {
      const appStatus = getPWADisplayMode();
      if (appStatus == "standalone") {
        isInstalled.value = true;
      } else {
        card.value = true;
      }
    };

    const goBack = async () => {
      await reloadInfo();
      card.value = false;
    };

    const toAbout = () => {
      router.replace({ name: "About" });
    };

    const lockMe = async () => {
      dialogHandler({
        title: "Confirm Lock?",
        message: "Security Pin is required to unlock.",
        persistent: true,
        ok: {
          label: "Understand",
          style: { "text-transform": "none" },
          color: "green",
        },
        cancel: {
          label: "Cancel",
          style: { "text-transform": "none" },
        },
        html: true,
      }).onOk(async () => {
        await lockApp();
      });
    };

    const lockApp = async () => {
      const wallet = await readWallet();
      const key = wallet.pack.key;
      isLocked.value = true;
      let secure = wallet.secure;
      secure["locked"] = true;
      await storage.set("secure", await Encrypt(secure, key));
      context.emit("reload");
    };

    const unlockMe = async () => {
      const passFunc = async () => {
        isLocked.value = false;
        await reloadInfo();
        context.emit("reload");
      };
      const failFunc = async () => {
        await reloadInfo();
      };
      if (appBlock.value == false) {
        await confirmPin(passFunc, failFunc);
      } else {
        dialogHandler({
          title: "You have been blocked",
          message: "You have violated our terms.",
          persistent: true,
          ok: {
            label: "OK",
            style: { "text-transform": "none" },
            color: "green",
          },
          html: true,
        }).onOk(async () => {
          window.location.reload(true);
        });
      }
    };

    const toReceive = () => {
      router.replace({ name: "Take" });
    };

    return {
      isInstalled,
      loaded,
      user,
      wallet,
      reloadInfo,
      username,
      wrapUsername,
      K,
      installHandler,
      card,
      toAbout,
      goBack,
      isLocked,
      lockMe,
      unlockMe,
      poclink,
      toReceive,
      iconSet,
      getStrAppVersion,
      getStrPublicKey,
      isDesktop,
    };
  },
  data: function () {
    return {
      windowWidth: 0,
      windowHeight: 0,
    };
  },
  mounted() {
    this.$nextTick(function () {
      window.addEventListener("resize", this.getWindowWidth);
      window.addEventListener("resize", this.getWindowHeight);
      this.getWindowWidth();
      this.getWindowHeight();
    });
  },
  methods: {
    getWindowWidth(event) {
      this.windowWidth = document.documentElement.clientWidth;
    },
    getWindowHeight(event) {
      this.windowHeight = document.documentElement.clientHeight;
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.getWindowWidth);
    window.removeEventListener("resize", this.getWindowHeight);
  },
};
</script>
