<template>
  <div v-if="!user">
    <div class="q-pa-none q-mb-xl" style="max-width: 700px">
      <q-list bordered class="rounded-borders">
        <q-expansion-item
          group="somegroup"
          icon="login"
          label="Login if you already have an account"
          default-opened
          header-class="text-primary"
          style="font-size: 1rem"
          @click="clearInput()"
        >
          <q-card>
            <q-card-section>
              <br />
              <q-input
                bottom-slots
                type="email"
                v-model="email"
                label="Email"
                style="font-size: 1.25rem"
              >
              </q-input>

              <q-input
                bottom-slots
                :type="isPwd ? 'password' : 'text'"
                v-model="password"
                label="Password"
                style="font-size: 1.25rem"
              >
                <template v-slot:append>
                  <q-icon
                    :name="isPwd ? 'visibility_off' : 'visibility'"
                    class="cursor-pointer"
                    @click="isPwd = !isPwd"
                  />
                </template>
              </q-input>

              <AppButton
                text="Login"
                :action="emailLoginHandler"
                :disabled="!pwdIsValid || !emailIsValid"
              />
              <q-btn flat no-caps :disable="!emailIsValid" label="Forget Password" @click="resetPassword(email)" />
            </q-card-section>
          </q-card>
        </q-expansion-item>
        <q-separator />
        <q-expansion-item
          group="somegroup"
          icon="person_outline"
          label="Sign up to create an account"
          header-class="text-primary"
          style="font-size: 1rem"
          @click="clearInput()"
        >
          <q-card>
            <q-card-section>
              <br />
              <q-input
                bottom-slots
                type="email"
                v-model="email"
                label="Email"
                style="font-size: 1.25rem"
              >
              </q-input>

              <q-input
                bottom-slots
                :type="isPwd ? 'password' : 'text'"
                v-model="password"
                label="Password"
                style="font-size: 1.25rem"
              >
                <template v-slot:append>
                  <q-icon
                    :name="isPwd ? 'visibility_off' : 'visibility'"
                    class="cursor-pointer"
                    @click="isPwd = !isPwd"
                  />
                </template>
              </q-input>

              <AppButton
                text="Sign Up"
                :action="emailSignupHandler"
                :disabled="!pwdIsValid || !emailIsValid"
              />
            </q-card-section>
          </q-card>
        </q-expansion-item>
      </q-list>
    </div>
    <p>or</p>
    <q-btn
      rounded
      color="blue"
      text-color="white"
      @click="loginHandler"
      no-caps
    >
      <div class="q-pa-sm">
        <q-icon left size="1.25rem" name="fab fa-google" />
        Login with Google
      </div>
    </q-btn>
  </div>
  <div v-else>
    <AppButton text="Show user" :action="showUser" />
    <AppButton text="Logout" :action="logoutHandler" />
  </div>
</template>

<script>
import { ref, computed } from "vue";
import useLogout from "../composables/useLogout";
import useLogin from "../composables/useLogin";
import getUser from "../composables/getUser";
import AppButton from "./AppButton.vue";
import * as K from "../constants/constants";
import { resetPassword } from "../composables/handlers";

export default {
  components: { AppButton },
  setup(props, context) {
    const { logout, outError } = useLogout();
    const { user } = getUser();
    const { error, login } = useLogin();
    const email = ref("");
    const password = ref("");
    const isPwd = ref(true);

    const loginHandler = async (
      _,
      m = "google",
      e = email.value,
      p = password.value
    ) => {
      await login("", m, e, p);
    };

    const showUser = () => {
      K.print("token:", user._rawValue._lat);
      K.print("uid:", user._value.uid);
      K.print("displayName:", user._value.displayName);
      K.print("url:", user._value.photoURL);
    };

    const logoutHandler = async () => {
      await logout();
      if (!outError.value) {
        K.print("user logged out");
      }
    };

    const clearInput = () => {
      password.value = "";
      email.value = "";
      isPwd.value = true;
    };

    const validateEmail = (email) => {
      return String(email)
        .toLowerCase()
        .match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        );
    };

    const emailLoginHandler = async () => {
      if (validateEmail(email.value)) {
        await login("", "login", email.value, password.value);
        context.emit("reload");
      }
    };

    const emailSignupHandler = async () => {
      if (validateEmail(email.value)) {
        await login("", "signup", email.value, password.value);
        context.emit("reload");
      }
    };

    return {
      loginHandler,
      user,
      showUser,
      logoutHandler,
      email,
      password,
      isPwd,
      clearInput,
      emailLoginHandler,
      emailSignupHandler,
      pwdIsValid: computed(() => password.value.length >= 6),
      emailIsValid: computed(() => validateEmail(email.value)),
      resetPassword,
    };
  },
};
</script>

<style scoped>
p {
  font-size: 1rem;
}

.clickSpan {
  cursor: pointer;
  text-decoration: underline;
}
</style>