import { projectAuth, gprovider } from "../secrets/fbsecret";
import * as K from "../constants/constants";
import { ref } from "vue"

const error = ref(null)

const login = async(_, mode, email, password) => {
    switch (mode) {
        case "google":
            try {
                let result = await projectAuth
                    .signInWithRedirect(gprovider)
                    // .signInWithPopup(gprovider)
                    // if (!result) {
                    //   throw new Error('Could not complete the login')
                    // }
                    // var credential = result.credential;
                    // var token = credential.accessToken;
                    // var user = result.user;
                error.value = null
                return true
            } catch (err) {
                // var errorCode = err.code;
                var errorMessage = err.message;
                // var email = err.email;
                // var credential = err.credential;
                error.value = 'Login invalid'
                K.error(errorMessage);
                return false
            }
            break;
        case "signup":
            try {
                await projectAuth.createUserWithEmailAndPassword(email, password);
                error.value = null
                return true
            } catch (err) {
                // var errorCode = err.code;
                var errorMessage = err.message;
                // var email = err.email;
                // var credential = err.credential;
                error.value = 'Login invalid'
                K.error(errorMessage);
                return false
            }
            break;
        case "login":
            try {
                await projectAuth.signInWithEmailAndPassword(email, password);
                error.value = null
                return true
            } catch (err) {
                // var errorCode = err.code;
                var errorMessage = err.message;
                // var email = err.email;
                // var credential = err.credential;
                error.value = 'Login invalid'
                K.error(errorMessage);
                return false
            }
            break;
        default:
            // code block
    }


};

const useLogin = () => {
    return { error, login }
}


export default useLogin